.preloader-wrapper {
  text-align: center;
  margin: 0 auto;
}

.container__loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.loader {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #4592c4;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}