@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);


.preloader-wrapper{text-align:center;margin:0 auto}.container__loader{display:flex;justify-content:center;align-items:center;height:80vh}.loader{border:10px solid #f3f3f3;border-top:10px solid #4592c4;border-radius:50%;width:60px;height:60px;animation:spin 2s linear infinite}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}
body{margin:0;padding:0;font-family:"Open Sans",sans-serif}.container{width:100%;padding-bottom:10px;margin:0 auto}.container__caught{width:100%;padding-bottom:10px;margin:0 auto;display:flex;flex-wrap:wrap}.pokemons{width:95%;padding:0;display:flex;flex-wrap:wrap;margin:0 auto;justify-content:center}.pokemons__card{width:25%;margin:3%;text-align:center;box-shadow:5px 5px 10px rgba(0,0,0,.2),-5px 5px 10px rgba(0,0,0,.2);padding-bottom:30px}.pokemons__card img{width:100%}.pokemons__card a{text-decoration:none;color:#000;font-size:24px}.pokemons__card__btn{background-color:#9bcc50;border:none;height:35px;width:80%;border-radius:3px}.pokemons__card__btn:focus{background-color:#9bcc50}@media screen and (max-width: 768px){.pokemons__card{width:40%}}@media screen and (max-width: 425px){.pokemons__card{width:90%}}@media screen and (min-width: 1440px){.root{width:100vw;height:100vh}.container{width:100vw;height:100vh;margin:0;padding:0}}
.loadMore{display:block;width:40%;margin:0 auto;height:30px;background-color:#b97fc9 !important;color:#fff;border:none;border-radius:3px}
.first{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;background:url(/static/media/background.330f4548.png) no-repeat center fixed;background-size:cover}.first__auth{width:35vw;height:50vh;display:flex;flex-direction:column;justify-content:center;align-items:center;background:rgba(5,5,5,.5);border-radius:15px}.first__auth__input{width:90%;display:flex;flex-direction:column;justify-content:center;align-items:center}.first__auth__input input{width:90%;padding:8px;margin:5px;border-radius:5px;border:none}.first__auth__operations{width:90%;display:flex;justify-content:center;align-items:center;padding-top:20px}.first__auth__operations button{width:40%;padding:10px;margin:5px}.first__auth__operations_login{background-color:#9bcc50;border:none;border-radius:5px}.first__auth__operations_register{background-color:#b97fc9;border:none;border-radius:5px}@media screen and (max-width: 426px){.first__auth{width:60vw;height:60vh}.first__auth__operations{flex-direction:column}.first__auth__operations button{width:80%}}@media screen and (max-width: 768px){.first__auth{width:50vw;height:60vh}}
.container__card{width:100vw;display:flex;justify-content:center;align-items:center;font-family:"Open Sans",sans-serif}.container__card .card__pokemon{display:flex;padding-top:40px}.container__card .card__img img{box-shadow:5px 5px 5px rgba(0,0,0,.1),-5px -5px 5px rgba(0,0,0,.1)}.container__card .card__info{width:30%;padding-left:40px}@media screen and (max-width: 767px){.container__card .card__pokemon{padding-top:0;flex-wrap:wrap;align-items:center;display:flex}.container__card .card__img{width:100vw}.container__card .card__img img{width:100vw;box-shadow:-5px 5px 5px rgba(0,0,0,.1)}.container__card .card__info{width:100vw}}
body{margin:0;padding:0}nav{background-color:#b97fc9}ul{display:flex;flex-direction:row}ul li{padding:10px;list-style:none}ul a{text-decoration:none;color:#fff}@media screen and (max-width: 426px){ul{flex-direction:column}li{padding:0}}
