@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

.container {
  width: 100%;
  padding-bottom: 10px;
  margin: 0 auto;
}

.container__caught {
  width: 100%;
  padding-bottom: 10px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.pokemons {
  width: 95%;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: center;


  &__card {
    width: 25%;
    margin: 3%;
    text-align: center;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2),
    -5px 5px 10px rgba(0, 0, 0, 0.2);
    padding-bottom: 30px;

    img {
      width: 100%
    }

    a {
      text-decoration: none;
      color: black;
      font-size: 24px;
    }

    &__btn {
      background-color: #9BCC50;
      border: none;
      height: 35px;
      width: 80%;
      border-radius: 3px;
    }
    &__btn:focus {
      background-color: #9BCC50;
    }


  }
}

@media screen and (max-width: 768px) {
  .pokemons {

    &__card {
      width: 40%;
    }
  }
}

@media screen and (max-width: 425px) {
  .pokemons {

    &__card {
      width: 90%;
    }
  }
}

@media screen and (min-width: 1440px) {
  .root {
    width: 100vw;
    height: 100vh;
  }

  .container {
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
  }

  
}